<script>
import { register, getcode } from '../servers/api';
export default {
	data() {
		return {
			serverVisible: false,
			legalVisible: false,
			checked: false,
			checkedbtn: true,
			logining: false,
			loading: false,
			getyzmcode: false,
			num: 60,
			sertiem: '',
			ruleForm: {
				name: '',
				yzm: '',
				phone: '',
				pass: '',
				checkPass: ''
			}
		};
	},
	mounted() {},
	methods: {
		handleCheckedCitiesChange() {
			if (this.checked == false) {
				// this.checkedbtn = true;
			} else {
				// this.checkedbtn = false;
			}
		},
		//
		submitForm(formName) {
			this.$refs[formName].validate(valid => {
				if (valid) {
					if (this.checked == false) {
						this.$message({
							showClose: true,
							message: '',
							type: 'warning'
						});
					} else {
						let params = new Object();
						params.username = this.ruleForm.name;
						params.password = this.ruleForm.pass;
						params.password2 = this.ruleForm.checkPass;
						params.phone = this.ruleForm.phone + '';
						params.code = this.ruleForm.yzm;
						register(params)
							.then(res => {
								if (res.status == 0) {
									this.$message({
										message: '',
										type: 'success'
									});
									sessionStorage.setItem(
										'id',
										JSON.stringify(res.msg.id)
									);
									sessionStorage.setItem(
										'user',
										JSON.stringify(res.msg.username)
									);
									this.$cookies.set(
										'user',
										res.msg.username,
										7 * 24 * 60 * 60
									);
									this.$cookies.set(
										'id',
										res.msg.id,
										7 * 24 * 60 * 60
									);
									this.$cookies.set(
										'token',
										res.token,
										7 * 24 * 60 * 60
									);
									setTimeout(() => {
										this.$router.push({ path: '/' });
									}, 1000);
								} else if (res.status == -900) {
									clearInterval(this.sertiem);
									this.getyzmcode = false;
									this.num = 60;
									this.$message({
										message: res.msg,
										type: 'warning'
									});
									setTimeout(() => {
										this.$router.push({
											path: '/phone_log',
											query: {
												phone: this.ruleForm.phone
											}
										});
									}, 1000);
								}
							})
							.catch(err => {});
					}
				}
			});
		},
		resetForm(formName) {
			this.$refs[formName].resetFields();
		},
		//
		sendyam() {
			let params = new Object();
			if (!this.ruleForm.phone) {
				this.$message.error('');
				return false;
			} else {
				params.phone = this.ruleForm.phone;
			}
			this.loading = true;
			this.getyzmcode = true;

			getcode(params)
				.then(res => {
					this.loading = false;
					if (res.status == 0) {
						this.$message({
							message: '',
							type: 'success'
						});
						this.getyzmcode = true;
						this.sertiem = setInterval(() => {
							this.num -= 1;
							if (this.num <= 0) {
								clearInterval(this.sertiem);
								this.num = 60;
								this.getyzmcode = false;
							}
						}, 1000);
					} else {
						this.getyzmcode = false;
					}
				})
				.catch(err => {
					this.loading = false;
					this.getyzmcode = false;
				});
		},
		gologin() {
			this.$router.push({
				path: '/'
			});
		},
		//
		show_service() {
			this.serverVisible = true;
		},
		//
		show_legal() {
			this.legalVisible = true;
		},
		//
		jiousername(rule, value, callback) {
			if (value === '') {
				callback(new Error(''));
			} else {
				var fsdusername = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{4,20}$/;
				if (fsdusername.test(value) === false) {
					callback(new Error(''));
				} else {
					callback();
				}
			}
		},
		//
		jiouserpwa(rule, value, callback) {
			var fspwd = /(?!^\d+$)(?!^[A-Za-z]+$)(?!^[^A-Za-z0-9]+$)(?!^.*[\u4E00-\u9FA5].*$)^\S{8,16}$/;
			if (value === '') {
				callback(new Error(''));
			} else {
				if (fspwd.test(value) === false) {
					callback(new Error(''));
				} else {
					if (this.ruleForm.checkPass !== '') {
						this.$refs.ruleForm.validateField('checkPass');
					}
					callback();
				}
			}
		},
		//
		jioqpwd(rule, value, callback) {
			if (value === '') {
				callback(new Error(''));
			} else if (value !== this.ruleForm.pass) {
				callback(new Error('!'));
			} else {
				callback();
			}
		},
		//
		jiophone(rule, value, callback) {
			if (value === '') {
				callback(new Error(''));
			} else {
				var fsdtel = /^[1]([3-9])[0-9]{9}$/;
				if (fsdtel.test(value) === false) {
					callback(new Error(''));
				} else {
					callback();
				}
			}
		},
		//
		jioyzm(rule, value, callback) {
			if (value === '') {
				callback(new Error(''));
			} else {
				var resyzm = /^\d{6}$/;
				if (resyzm.test(value) === false) {
					callback(new Error(''));
				} else {
					callback();
				}
			}
		}
	},
	mounted() {}
};
</script>

<style lang="scss" scoped>
.contendst {
	width: 100%;
	height: 100%;
	background: url(../assets/img/login.png);
	background-size: 100% 100%;

	.titletext {
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		.demo-ruleForm {
			width: 369px;
			height: 556px;
			background: #fff;
			padding: 57px 68px 0 68px;
			position: fixed;
			top: 50%;
			margin-top: -306.5px;
			right: 15%;
		}
	}
}
</style>



